<template>
  <v-container class="fill-height" justify-center fluid>
    <v-row align="center" justify="center">
      <v-col cols="12" sm="8" md="4">
        <v-card class="elevation-12" v-if="0===state">
          <v-toolbar color="primary" dark flat>
            <v-toolbar-title>Forgot Password</v-toolbar-title>
          </v-toolbar>
          <v-card-text>
            Please enter your email address here, so we can send email with
            password reset link.
            <v-form>
              <v-text-field label="Email" prepend-icon="mdi-email"
                :readonly="loading" type="email" v-model="email"
                :error="emailErr" clearable />
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-spacer />
            <v-btn color="primary" :disabled="loading"
              @click="cancelClick"
            >Cancel</v-btn>
            <v-btn color="primary" :loading="loading"
              :disabled="!inputOK" @click="sendClick"
            >Send</v-btn>
          </v-card-actions>
        </v-card>
        <v-card class="elevation-12" v-if="1===state">
          <v-card-text>
            An email message with reset password link has been sent
            to <strong>{{ email }}</strong>. Please check the email and click
            the link to reset your password.
          </v-card-text>
          <v-card-actions>
            <v-spacer />
            <v-btn color="primary" to="/login" text>Go to login screen</v-btn>
          </v-card-actions>
        </v-card>
        <v-card class="elevation-12" v-if="2===state">
          <v-card-text>
            Ups! Something went wrong!!!
          </v-card-text>
          <v-card-actions>
            <v-spacer />
            <v-btn color="primary" text @click="state=0">Try again</v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
"use strict"

import { validateEmail } from "@/lib/utils"
import { requestPwResetLink } from "@/lib/auth"

export default {
  name: "Forgot",
  data: () => ({
    email: "",
    emailErr: false,
    loading: false,
    state: 0
  }),
  computed: {
    inputOK() {
      try {
        if (0 === this.email.length) return false
        return true
      } catch {
        return false
      }
    },
  },
  created() {
    //
  },
  methods: {
    cancelClick() {
      this.$router.go(-1)
    },
    sendClick: async function() {
      this.emailErr = false
      let isValid = validateEmail(this.email)
      if (!isValid) {
        this.emailErr = true
        return;
      }
      this.loading = true
      let resp = await requestPwResetLink(this.email)
      this.loading = false
      if (resp.code === 200) {
        this.state = 1
      } else {
        this.state = 2
        console.log(resp)
      }
    },
  }
}
</script>
